<template>
  <div class="project-info" onmousedown="return false" onselectstart="return false">
    <!--Header/NavBar-->
    <header>
      <div class="container">
        <a href="/" class="btn-soft">Home</a>
      </div>
    </header>
    <!--Project-->
    <section class="project py-5">
      <div class="container">
        <div
          class="row align-items-md-center py-4"
          v-for="project in getData($route.params.projectId)"
          :key="project.id"
        >
          <div class="col-md-6 mb-4 text-center">
            <img
              class="img-fluid d-md-block mr-md-auto"
              :src="
                require('../assets/portfolio/mockups/' + project.img_mockup)
              "
            />
          </div>
          <div class="project-info-content col-md-6 mb-4 mb-md-0">
            <h2 class="project-info-heading">
              <span>{{ project.title }}</span>
            </h2>
            <p class="project-info-p">{{ project.description }}</p>
            <h4>Features:</h4>
            <ul v-for="(feature, i) in project.features" :key="i">
              <li>{{ feature }}</li>
            </ul>
            <br>
            <div v-if="project.title == 'Covid-19 Tracking App'">
              <a :href="require('../assets/project/' + project.url).default" class="btn-soft" download="Covid-19 Tracking App Demo">Download App</a>
            </div>
            <div v-else-if="project.type == 'Android App'">
              <a :href="project.url" class="btn-soft" target="_blank" download>Download App</a>
            </div>
             <div v-else-if="project.type == 'Android Game'">
              <a :href="project.url" class="btn-soft" target="_blank" download>Download Game</a>
            </div>
            <div v-else-if="project.type == 'Android Library'">
              <a :href="project.url" class="btn-soft" target="_blank" download>Check Library</a>
            </div>
            <div v-else-if="project.type == 'Website'">
              <a :href="project.url" class="btn-soft" target="_blank" download>View Website</a>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import jsonProjects from '@/assets/projects.json';

export default {
  name: 'ProjectInfo',
  data() {
    return {
      projects: jsonProjects,
    };
  },
  methods: {
    getData(id) {
      const data = this.projects;
      return data.filter((item) => {
        return item.id == id;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../scss/_projects';
</style>
