<script>
import jsonProjects from '@/assets/projects.json'

export default {
  name: 'Portfolio',
  methods: {
    scrollTo(element) {
      //hide navList
      const navList = document.querySelector('.nav-list');
      navList.classList.toggle('active');

      const el = document.querySelector('#' + element);
      el.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
    },
    navToggler() {
      const openMenu = document.querySelector('.menu-toggler');
      const navList = document.querySelector('.nav-list');

      openMenu.addEventListener('click', function() {
        navList.classList.toggle('active');
      });
    },
    submitInfo(id, title){
      this.$router.push({
        name: 'ProjectInfo',
        params: {
          projectId: id,
          projectTitle: title
        }
      });

    }
  },
  mounted: function() {
    this.navToggler();
  },
  data() {
    return {
      skills: [
        {
          img: 'skills_android.png',
          title: 'Android Development',
          description: 'Java, Kotlin, Material Design, Android Studio, Room Persistence Library, Retrofit2, Gson, RxJava2, RxAndroid, Glide, Butterknife, Firebase, SQLite/MySQL, and Mapbox.'
        },
        {
          img: 'skills_web.png',
          title: 'Web Development',
          description: 'Responsive Web Design, HTML/HTML5, CSS/CSS3, JavaScript, Bootstrap 4, SASS/SCSS, PHP, VueJs, REST API, Postman, and Visual Studio Code.'
        },
        {
          img: 'skills_system.png',
          title: 'System Development',
          description: 'C# .NET, Visual Basic .NET, MySQL, Bunifu UI, Metro UI, and Visual Studio.'
        },
        {
          img: 'skills_others.png',
          title: 'Others',
          description: 'Git, Github, Adobe Photoshop, Hardware/Software/Network Troubleshooting.'
        }
      ],
      projects : jsonProjects

    };
  },
};
</script>

<style lang="scss" scoped>
